@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900");
@font-face {
  font-family: 'Libre Bodoni';
  src: url("fonts/LibreBodoniBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal; }

@font-face {
  font-family: 'Libre Bodoni';
  src: url("/assets/fonts/libre-bodoni/0f49c646c8f12a9dbd4730215d10a2a8/629d903227943776cbe3783d8cc1e84b/LibreBodoniBoldItalic.ttf") format("truetype");
  font-weight: bold;
  font-style: italic; }

@font-face {
  font-family: 'Libre Bodoni';
  src: url("/assets/fonts/libre-bodoni/0f49c646c8f12a9dbd4730215d10a2a8/bb68fe30ffd78ce5863852f076341165/LibreBodoniItalic.ttf") format("truetype");
  font-weight: normal;
  font-style: italic; }

@font-face {
  font-family: 'Libre Bodoni';
  src: url("fonts/LibreBodoniRegular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal; }

.flat-button {
  display: inline-block;
  padding: .2em .8em;
  border: 2px solid white;
  border-color: var(--color);
  border-radius: .3em;
  color: white;
  color: var(--color);
  font-size: 130%;
  font-weight: 400;
  text-decoration: none; }
  .flat-button:hover {
    background: var(--color);
    color: var(--background); }

body {
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  margin: 0;
  font: 300 106%/1.5 Roboto, "Helvetica Neue", sans-serif;
  font-kerning: auto;
  hyphens: auto; }

a {
  color: #25aff4;
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

p {
  hyphens: auto; }

dd + dt {
  margin-top: 1.5em; }

pre, code {
  background: whitesmoke;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  tab-size: 4;
  hyphens: none;
  white-space: pre; }

/* Code blocks */
pre {
  padding: 1em 1.5em;
  margin: .5em -1.5em;
  overflow: auto;
  background: floralwhite;
  box-shadow: 0 0 10em #ffeacc inset;
  clip-path: polygon(0 0, 100% 0.5em, calc(100% - .5em) calc(100% - 1em), 0 100%); }
  @supports not (clip-path: polygon(0 0)) {
    pre {
      border-radius: .5em; } }
  pre > code {
    background: transparent; }

:not(pre) > code {
  background: transparent;
  font-weight: bold;
  color: #505662; }

body > section > hr {
  height: .5em;
  width: 5em;
  margin: 1.5rem 0 1rem;
  background: #ff0080;
  border: none; }

input {
  font: inherit;
  color: inherit;
  border: none;
  border-bottom: 0.1em solid rgba(0, 0, 0, 0.4); }

body > header,
body > section,
body > footer {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  padding: 1rem; }

body > header {
  position: relative;
  z-index: 1;
  display: flex;
  padding: .8em 1em .6em;
  justify-content: space-between;
  flex-flow: row;
  clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 1.5em), 0% 100%);
  background: #222;
  color: white; }
  body > header nav {
    display: flex;
    font-weight: 700; }
    body > header nav a {
      display: block;
      padding: 0 1em;
      border-radius: 2em;
      line-height: 2; }
      body > header nav a:hover {
        background: white;
        color: black;
        mix-blend-mode: screen;
        text-decoration: none; }
  body > header .logo {
    margin: 0; }
    body > header .logo img {
      height: 2rem; }
  body > header + h2 {
    padding: 1.5em 1em .5em;
    margin-top: -1.5em;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% calc(100% - 1em));
    background: #ff0080;
    color: white;
    text-align: right;
    font-size: 250%;
    font-weight: 200;
    line-height: 1; }

body > section {
  flex: 1; }
  body > section > h1 {
    font: bold 200% Didot, "Didot LT STD", "Bodoni 72", Bodoni, "Bodoni MT", "Libre Bodoni", "Hoefler Text", Cambria, Georgia, serif;
    color: #ff9500; }
  body > section > h2,
  body > section > section > h1 {
    font: bold 150% Didot, "Didot LT STD", "Bodoni 72", Bodoni, "Bodoni MT", "Libre Bodoni", "Hoefler Text", Cambria, Georgia, serif;
    color: #ff0080; }
  body > section > * {
    width: 900px;
    max-width: 90vw; }

body > footer {
  display: flex;
  flex-flow: row wrap;
  padding: 3em 1em 1em;
  padding: 3em calc(50% - 450px) 1em;
  background: #e9eaed;
  color: rgba(0, 0, 0, 0.8);
  font-size: 80%;
  clip-path: polygon(0% 0%, 100% 2.5em, 100% 100%, 0% 100%); }
  body > footer > div {
    flex: 1;
    margin: 0 1em 0 .5em; }
  body > footer p {
    margin: .5em; }
  body > footer a {
    color: inherit;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.7); }
  body > footer a.logo {
    height: 3em;
    margin: 0 .5em; }
    body > footer a.logo img {
      height: 100%; }

.notice {
  padding: .5em 1em;
  border-radius: .3em;
  background: #d3effd;
  width: auto; }
  .notice a {
    font-weight: bold; }

.note, .tip {
  border-left: 0.5em solid #66c7f7;
  padding-left: .5em;
  margin-left: -1em; }
  .note::before, .tip::before {
    content: "Note: ";
    font-weight: bold;
    color: #25aff4; }

.tip::before {
  content: "Tip: "; }
